<template>
  <div class="home">
    <div class="text">
      <h1>Welcome to Your Password Manager</h1>
      <p>
        {{ showFull ? shortenedText : text }}
        <span class="more" @click="showFull = !showFull">
          {{ showFull ? "More" : "Less" }}
        </span>
      </p>
    </div>
    <img alt="Vue logo" src="../assets/logo.png" />

    <!-- حقل الإدخال وكلمة المرور -->
    <div class="prompt">
      <div>
        <input
          class="in-prom"
          v-model="userInput"
          :type="inputType"
          placeholder="Enter your password"
        />
        <button @click="togglePasswordVisibility">
          {{ inputType === "password" ? "Show" : "Hide" }}
        </button>
      </div>
      <button @click="validatePassword" class="sub">Submit</button>

      <!-- رسالة الخطأ عند إدخال كلمة مرور خاطئة -->
      <p v-if="isInvalid" class="error">The password is wrong</p>
    </div>

    <!-- عرض معلومات كلمة المرور بعد التحقق -->
    <div v-if="isValid" class="infos">
      <AllPassword
        v-for="info in infos"
        :key="info.id"
        :name="info.name"
        :userName="info.userName"
        :gmailName="info.gmailName"
        :pass="info.pass"
      />
    </div>
  </div>
</template>

<script>
import AllPassword from "@/components/AllPassword.vue";
import passInfos from "@/components/JSON/info.json";

export default {
  name: "HomeView",
  components: {
    AllPassword,
  },
  data: function () {
    return {
      infos: passInfos,
      userInput: "",
      pass: "password", // كلمة المرور الافتراضية
      isValid: false,
      isInvalid: false,
      inputType: "password", // نوع الإدخال الافتراضي
      text: `This is a secure space where you can manage all your important passwords
        in one place. Simply enter your password to access your stored
        information, and keep your accounts safe. You can view, edit, and manage
        your passwords easily. This tool provides you with a convenient and
        organized way to store your credentials securely, ensuring you never
        forget any important details. Take control of your passwords today and
        enjoy peace of mind knowing your data is safely stored.`,
      showFull: true,
    };
  },
  computed: {
    shortenedText() {
      return this.text.substring(0, 100);
    },
  },
  methods: {
    // تبديل بين إظهار وإخفاء كلمة المرور
    togglePasswordVisibility() {
      this.inputType = this.inputType === "password" ? "text" : "password";
    },

    // التحقق من كلمة المرور المدخلة
    validatePassword() {
      if (this.userInput === this.pass) {
        this.isValid = true; // كلمة المرور صحيحة
        this.isInvalid = false;
      } else {
        this.isValid = false;
        this.isInvalid = true; // كلمة المرور خاطئة
      }
    },
  },
};
</script>

<style scoped>
@import url("@/components/scss/home/home.css");
</style>
